//
import React, { Component } from 'react';
import { DragSource } from 'react-dnd';
import data from './data.json';

const components = Object.keys(data);
class Source extends Component {
  render() {
    return (
      components.map(component => {
        return (
          <div key={component} className="component-box">
            <Item component={component} index={this.props.index} />
            <p>{data[component]}</p>
          </div>
        )
      })
    )
  }
}

const spec = {
  /**
	 * 
	 * @param {*} props  props
	 * @param {*} monitor DragSourceMonitor 
   * @returns {*} item
	 */
  beginDrag(props, monitor) {
    //
    let width = 15;
    let height = 15;
    let color = '#000';
    let html, name, label = '';
    let background = '#1890ff';
    let borderColor = '#d9d9d9';
    let top = -5;
    //
    if (props.component === "INPUT") {
      width = 160;
      height = 30;
      html = name = label = '';
      background = '#fff';
    } else if (props.component === "SELECT") {
      width = 160;
      height = 30;
      html = name = label = '';
    } else if (props.component === "BUTTON") {
      width = 100;
      height = 30;
      html = name = label = '';
      borderColor = '#1890ff'
    } else if (props.component === "UPLOAD") {
      width = 120;
      height = 30;
      html = name = label = '';
      background = "#fff";
    } else if (props.component === "LABEL") {
      width = 'auto';
      height = 20;
      html = name = label = '';
      background = 'none'
    } else if (props.component === "RADIO") {
      html = name = label = '';
      top = -0;
    } else if (props.component === "CHECKBOX") {
      html = name = label = '';
      top = -0;
    } else if (props.component === "DATE") {
      width = 'auto';
      height = 20;
      html = name = label = '';
      background = 'none'
    }
    //
    let scrolltop, scrollleft;
    if (document.documentElement && document.documentElement.scrollTop) {
      scrolltop = document.documentElement.scrollTop;
      scrollleft = document.documentElement.scrollLeft;
    } else if (document.body) {
      scrolltop = document.body.scrollTop;
      scrollleft = document.body.scrollLeft;
    }
    //item
    return {
      component: props.component,
      position: null,  //
      pointer: monitor.getInitialClientOffset(),  //
      location: monitor.getInitialSourceClientOffset(),  //
      width: width,
      height: height,
      color: color,
      fontSize: 14,
      borderRadius: 4,
      background: background,
      html: html,
      borderColor: borderColor,
      options: null,
      scrolltop: scrolltop,
      scrollleft: scrollleft,
      name: name,
      top: top,
      label: label
    };
  },
  /**
	 * 
   * @param {*} props  props
	 * @param {*} monitor DragSourceMonitor 
	 */
  isDragging(props, monitor) {
    const item = monitor.getItem();
    if (monitor.getClientOffset() !== null) {
      item.position = monitor.getClientOffset() //
    }
  },
};
/**
   *  connect  monitor 
	 * @param {*} connect  DOM  react-dnd  backend 
	 * @param {*} monitor DragSourceMonitor 
   * @returns {*}  props 
	 */
const collect = (connect, monitor) => {
  return {
    connectDragSource: connect.dragSource(),
    isDragging: monitor.isDragging(),
    item: monitor.getItem()
  };
}

const Item = DragSource("drag-items", spec, collect)(props => {
  const { connectDragSource, component } = props;
  if (component === "INPUT") {
    // connectDragSource  DOM 
    return connectDragSource(
      <img src={require('../../assets/images/input.png')} alt="" />
      // <span className="item">
      //   <Input placeholder=""/>
      // </span>
    )
  } else if (component === "RADIO") {
    return connectDragSource(
      <img src={require('../../assets/images/radio.png')} alt="" />
      // <span className="radioitem">
      //   <Radio style={{ marginBottom: '10px' }} />
      // </span>
    )
  } else if (component === "SELECT") {
    return connectDragSource(
      <img src={require('../../assets/images/select.png')} alt="" />
      // <span className="item" style={{ width: '160px' }}>
      //   <Select placeholder="" />
      // </span>
    )
  } else if (component === "BUTTON") {
    return connectDragSource(
      <img src={require('../../assets/images/button.png')} alt="" />
      // <span className="item">
      //   <Button type="primary"></Button>
      // </span>
    )
  } else if (component === "CHECKBOX") {
    return connectDragSource(
      <img src={require('../../assets/images/checkbox.png')} alt="" />
      // <span className="radioitem">
      //   <Checkbox type="primary" style={{ marginBottom: '10px' }}></Checkbox>
      // </span>
    )
  } else if (component === "UPLOAD") {
    return connectDragSource(
      <img src={require('../../assets/images/upload.png')} alt="" />
      // <span className="radioitem">
      //   <Upload>
      //     <Button>
      //       <Icon type="upload" /> Upload
      //       </Button>
      //   </Upload>
      // </span>
    )
  } else if (component === "LABEL") {
    return connectDragSource(
      <img src={require('../../assets/images/label.png')} alt="" />
      // <span className="radioitem">
      //   
      // </span>
    )
  } else if (component === "DATE") {
    return connectDragSource(
      <img src={require('../../assets/images/date.png')} alt="" />
    )
  }
});

export default Source